import React, { Component, Suspense } from 'react';
import Home from './01-Home';
import Navbar from './00-Navbar';

const InfoSection = React.lazy(() => import('./02-InfoSection'));
const DataSection = React.lazy(() => import('./03-DataSection'));
const InfographicSection = React.lazy(() => import('./04-InfographicsSection'));
const ParallaxSection = React.lazy(() => import('./05-ParallaxSection'));
const Footer = React.lazy(() => import('./06-Footer'));

class Wrapper extends Component {
  render() {
    return (
      <div>
        <Navbar />
        <Home />
        <Suspense fallback={<div />}>
        <InfoSection />
        <DataSection />
        <InfographicSection />
        <ParallaxSection />
        <Footer/>
        </Suspense>
      </div>
    );
  }
}

export default Wrapper;
